<template>
  <div>
    <navbar baseName="home"></navbar>
    <sidebar></sidebar>
    <div class="top">
      <div>
        <select-month @date="setMonthData"></select-month>
        <value-card name=" " :value="userTotal"></value-card>
        <value-card name=" " :value="previewTotal"></value-card>
      </div>
      <graph-card name=" " :data="userData" :label="labelData" :min="200" :max="600"></graph-card>
      <graph-card name=" " :data="previewData" :label="labelData"  :min="200" :max="600"></graph-card>
      <list-card name=""></list-card>
    </div>
  </div>
</template>
<script>
import Navbar from "../organisms/Navbar";
import Sidebar from "../organisms/Sidebar";
import ValueCard from "../molecules/ValueCard";
import GraphCard from "../molecules/GraphCard";
import ListCard from "../molecules/ListCard";
import SelectMonth from "../molecules/SelectMonth";
export default {
  components: {
    Navbar,
    Sidebar,
    ValueCard,
    GraphCard,
    ListCard,
    SelectMonth
  },
  data() {
    return {
      daysNum: 0,
      userData: [],
      previewData: [],
      labelData: []
    };
  },
  created() {
    this.setMonthData(new Date());
  },
  computed: {
    getUserData() {
      // TODO: fix getting from server data
      return [...Array(this.daysNum)].map(
        () => 300 + Math.floor(Math.random() * Math.floor(500 - 300))
      );
    },
    userTotal() {
      return this.userData.reduce((prev, current) => prev + current);
    },
    getPreviewData() {
      // TODO: fix getting from server data
      return [...Array(this.daysNum)].map(
        () => 300 + Math.floor(Math.random() * Math.floor(500 - 300))
      );
    },
    previewTotal() {
      return this.previewData.reduce((prev, current) => prev + current);
    }
  },
  methods: {
    getDays(date) {
      return new Date(date.getFullYear(), date.getMonth() + 1, 0).getDate();
    },
    getLabelList(date) {
      date.setDate(0);
      return [...Array(this.daysNum)].map(() => {
        date.setDate(date.getDate() + 1);
        return date.toLocaleDateString();
      });
    },
    setMonthData(date) {
      this.daysNum = this.getDays(date);
      this.userData = this.getUserData;
      this.previewData = this.getPreviewData;
      this.labelData = this.getLabelList(date);
    }
  }
};
</script>
<style scoped lang="scss">
.top {
  padding: 66px 0 0 200px;
}
</style>